.exchangerForm {
    background-color: rgb(237, 250, 250);
    margin: 5% 10%;
    padding: 5% 5%;
    border-radius: 20px;
    box-shadow: 0 0 20px 2px rgb(117 117 117);
    flex-basis: 1000px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.exchangerForm__fieldset {
    padding: 50px;
    border-radius: 20px;
    flex-basis: 800px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.exchangerForm__legend {
    padding: 0 5%;
    text-align: center;
}

@media (max-width:500px) {
    .exchangerForm__fieldset {
        padding: 50px 8vw;
    }
}

@media (max-width:350px) {
    .exchangerForm__fieldset {
        padding: 50px 0;
    }
    .exchangerForm__legend {
        padding: 0 1%;
        text-align: center;
    }
}
